import React from "react";
import s from './MyPosts.module.css'
import Post from "./Post/Post";
import {profilePageType} from "../../Redux/State";


type profilePageProps = {
    profilePage: profilePageType
    addColbackPost: (message: string) => void
}


function MyPosts(props: profilePageProps) {

    let postsElement = props.profilePage.posts.map(p => <Post message={p.message} like={p.likesCount} img={p.img}/>)

    let newPostElement = React.createRef<HTMLTextAreaElement>()

    let addPost = () => {
        debugger
        if (newPostElement.current) {
            props.addColbackPost(newPostElement.current.value)
        }
    }

    return <div className={s.myPosts}>
        <div>My posts</div>
        <textarea cols={80} rows={5} ref={newPostElement}>Add post</textarea>
        <div>
            <button onClick={addPost}>Send</button>
        </div>
        {postsElement}
    </div>
}

export default MyPosts