import React from "react";
import s from "./Friends.module.css"
import { navbarType} from "../../Redux/State";

export type FriendsProps = {
    state: navbarType
}


export function Friends(props: FriendsProps) {

    let navbarElements = props.state.friends.map(f => {
        return <div><img src={f.src} alt=""/><p>{f.name}</p></div>
    })


    return <div className={s.friendWrapper}>
        <div className={s.friends}>
            <h2>Friends</h2>
            <div className={s.avatarsFriends}>
                {navbarElements}
            </div>
        </div>
    </div>
}

export default Friends